import React from 'react'
import s from './Nav.module.scss'
import {NavLink} from "react-router-dom";

const Nav = () => {
    return (
        <nav className={s.nav}>
            <ul className={s.list}>
                <NavLink to='/Home' className='icon-home'><span>HOME</span></NavLink>
                <NavLink to='/Skills' className='icon-embed2'><span>SKILLS</span></NavLink>
                <NavLink to='/About' className='icon-user-secret'><span>ABOUT ME</span></NavLink>
                <NavLink to='/Portfolio' className='icon-monitor'><span>PORTFOLIO</span></NavLink>
                <NavLink to='/Blog' className='icon-book'><span>BLOG</span></NavLink>
                <NavLink to='/Contacts' className='icon-phone'><span>CONTACTS</span></NavLink>
            </ul>
        </nav>
    )
}

export default Nav